import React from 'react'
import NavDropdown from 'react-bootstrap/NavDropdown'
import Offcanvas from 'react-bootstrap/Offcanvas'
import Form from 'react-bootstrap/Form'
import FormControl from 'react-bootstrap/FormControl'
import Button from 'react-bootstrap/Button'
import  Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import './MobNavbar.css'
import { Link } from 'react-router-dom'
function MobNavbar() {
    return (
        <>
          <Navbar.Toggle aria-controls="offcanvasNavbar" />
                
                <Navbar.Offcanvas
                    id="offcanvasNavbar"
                    aria-labelledby="offcanvasNavbarLabel"
                    placement="start"
                >
                <Offcanvas.Header closeButton>
                    <Offcanvas.Title id="offcanvasNavbarLabel">Storkini</Offcanvas.Title>
                </Offcanvas.Header>
                <Offcanvas.Body>
                    <Nav className="justify-content-end flex-grow-1 pe-3">
                    <ul className='ul-menu'>
                    <Link to={`/about`}><li className='list-item hvr-grow'>O nama</li></Link>
                    <li className='list-item hvr-grow'>Marketing</li>
                    <Link to={`/contact`}><li className='list-item hvr-grow'>Kontakt</li></Link>
                </ul>
                   
                    </Nav>
                    <Form className="d-flex">
                    <FormControl
                        type="search"
                        placeholder="Ukucajte ime objekta"
                        className="me-2 mt-3"
                    />
                    <Button className="mt-3" variant="secondary">Pretraga</Button>
                    </Form>
                </Offcanvas.Body>
                </Navbar.Offcanvas>  
        </>
    )
}

export default MobNavbar
